'use strict';
import { AfterViewChecked, Component, Input, Output, EventEmitter, OnInit, ViewChild,
 forwardRef, AfterViewInit, ElementRef } from '@angular/core';
import { ControlValueAccessor, FormControl, NG_VALUE_ACCESSOR } from '@angular/forms';

import { MdbCompleterDirective } from '../directives/completer.directive';
import { CompleterData } from '../services/completer-data.service';
import { CompleterService } from '../services/completer.service';
import { CompleterItem } from './completer-item.component';
import { MAX_CHARS, MIN_SEARCH_LENGTH, PAUSE, TEXT_SEARCHING, TEXT_NO_RESULTS } from '../globals';


import 'rxjs/add/operator/catch';

const noop = () => { };

const COMPLETER_CONTROL_VALUE_ACCESSOR = {
  provide: NG_VALUE_ACCESSOR,
  useExisting: forwardRef(() => CompleterComponent),
  multi: true
};



export class CompleterComponent implements OnInit, ControlValueAccessor, AfterViewChecked, AfterViewInit {
   public dataService: CompleterData;
   public inputName = '';
   public inputId = '';
   public pause = PAUSE;
   public minSearchLength = MIN_SEARCH_LENGTH;
   public maxChars = MAX_CHARS;
   public overrideSuggested = false;
   public clearSelected = false;
   public clearUnselected = false;
   public fillHighlighted = true;
   public placeholder = '';
   public matchClass: string;
   public fieldTabindex: number;
   public autoMatch = false;
   public disableInput = false;
   public inputClass: string;
   public autofocus = false;
   public openOnFocus = false;
   public initialValue: any;
   public autoHighlight = false;
   public label: string;

  public focused = false;


   public selected = new EventEmitter<CompleterItem>();
   public highlighted = new EventEmitter<CompleterItem>();
   public blur = new EventEmitter<void>();
   public focusEvent = new EventEmitter<void>();
   public opened = new EventEmitter<boolean>();
   public keyup: EventEmitter<any> = new EventEmitter();
   public keydown: EventEmitter<any> = new EventEmitter();

   public completer: MdbCompleterDirective;
   public mdbInput: ElementRef;

  public searchStr = '';
  public control = new FormControl('');

  private displaySearching = true;
  private displayNoResults = true;
  private _onTouchedCallback: () => void = noop;
  private _onChangeCallback: (_: any) => void = noop;
  private _focus = false;
  private _open = false;
  private _textNoResults = TEXT_NO_RESULTS;
  private _textSearching = TEXT_SEARCHING;

  constructor(private completerService: CompleterService) { }

  get value(): any { return this.searchStr; };

  set value(v: any) {
    if (v !== this.searchStr) {
      this.searchStr = v;
    }
    // Propagate the change in any case
    this._onChangeCallback(v);
  }

  public ngAfterViewInit() {
    if (this.autofocus) {
      this._focus = true;
    }
  }

  public ngAfterViewChecked(): void {
    if (this._focus) {
      this.mdbInput.nativeElement.focus();
      this._focus = false;
    }
  }

  public onTouched() {
    this._onTouchedCallback();
  }

  public writeValue(value: any) {
    this.searchStr = value;
  }

  public registerOnChange(fn: any) {
    this._onChangeCallback = fn;
  }

  public registerOnTouched(fn: any) {
    this._onTouchedCallback = fn;
  }

  
  public set datasource(source: CompleterData | string | Array<any>) {
    if (source) {
      if (source instanceof Array) {
        this.dataService = this.completerService.local(source);
      } else if (typeof (source) === 'string') {
        this.dataService = this.completerService.remote(source);
      } else {
        this.dataService = source;
      }
    }
  }

  
  public set textNoResults(text: string) {
    if (this._textNoResults !== text) {
      this._textNoResults = text;
      this.displayNoResults = this._textNoResults && this._textNoResults !== 'false';
    }
  }

  
  public set textSearching(text: string) {
    if (this._textSearching !== text) {
      this._textSearching = text;
      this.displaySearching = this._textSearching && this._textSearching !== 'false';
    }
  }

  public ngOnInit() {
    this.completer.selected.subscribe((item: CompleterItem) => {
      this.selected.emit(item);
    });
    this.completer.highlighted.subscribe((item: CompleterItem) => {
      this.highlighted.emit(item);
    });
    this.completer.opened.subscribe((isOpen: boolean) => {
      this._open = isOpen;
      this.opened.emit(isOpen);
    });
  }

  public onBlur() {
    this.blur.emit();
    this.onTouched();
    if (this.searchStr === undefined || this.searchStr === '') {
      this.focused = false;
    }
  }

  public onFocus() {
    this.focusEvent.emit();
    this.onTouched();
    this.focused = true;
  }

  public onKeyup(event: any) {
    this.keyup.emit(event);
  }

  public onKeydown(event: any) {
    this.keydown.emit(event);
  }

  public onChange(value: string) {
    this.value = value;
  }

  public open() {
    this.completer.open();
  }

  public close() {
    this.completer.clear();
  }

  public focus(): void {
    if (this.mdbInput) {
      this.mdbInput.nativeElement.focus();
    } else {
      this._focus = true;
    }
  }

  public isOpen() {
    return this._open;
  }
static decorators: DecoratorInvocation[] = [
{ type: Component, args: [{
  selector: 'mdb-completer',
  templateUrl: './completer.component.html',
  providers: [COMPLETER_CONTROL_VALUE_ACCESSOR]
}, ] },
];
/** @nocollapse */
static ctorParameters: () => ({type: any, decorators?: DecoratorInvocation[]}|null)[] = () => [
{type: CompleterService, },
];
static propDecorators: {[key: string]: DecoratorInvocation[]} = {
'dataService': [{ type: Input },],
'inputName': [{ type: Input },],
'inputId': [{ type: Input },],
'pause': [{ type: Input },],
'minSearchLength': [{ type: Input },],
'maxChars': [{ type: Input },],
'overrideSuggested': [{ type: Input },],
'clearSelected': [{ type: Input },],
'clearUnselected': [{ type: Input },],
'fillHighlighted': [{ type: Input },],
'placeholder': [{ type: Input },],
'matchClass': [{ type: Input },],
'fieldTabindex': [{ type: Input },],
'autoMatch': [{ type: Input },],
'disableInput': [{ type: Input },],
'inputClass': [{ type: Input },],
'autofocus': [{ type: Input },],
'openOnFocus': [{ type: Input },],
'initialValue': [{ type: Input },],
'autoHighlight': [{ type: Input },],
'label': [{ type: Input },],
'selected': [{ type: Output },],
'highlighted': [{ type: Output },],
'blur': [{ type: Output },],
'focusEvent': [{ type: Output },],
'opened': [{ type: Output },],
'keyup': [{ type: Output },],
'keydown': [{ type: Output },],
'completer': [{ type: ViewChild, args: [MdbCompleterDirective, ] },],
'mdbInput': [{ type: ViewChild, args: ['mdbInput', ] },],
'datasource': [{ type: Input },],
'textNoResults': [{ type: Input },],
'textSearching': [{ type: Input },],
};
}

interface DecoratorInvocation {
  type: Function;
  args?: any[];
}
